<template>
  <div class="christmas-activity">
    <div class="draw-btn" v-if="showDraw" @click="drawClick"></div>
    <div class="sign-btn" v-if="!showDraw" @click="signClick"></div>
    <h4 class="title"><span>Ê¥ªÂä®ËØ¥Êòé</span></h4>
    <ol class="rules">
      <li data-i="1">Ê¥ªÂä®Êó∂Èó¥Ôºö <br>12Êúà23Êó•00Ôºö00-12Êúà27Êó•24Ôºö00Ôºõ</li>
      <li data-i="2">ÂèÇ‰∏éÁî®Êà∑Ôºö <br>2019Âπ¥12Êúà23Êó•ÂâçÔºàÂê´23Êó•ÔºâËÆ§ËØÅÁöÑÊäïËµÑ‰∫∫Ôºõ</li>
      <li data-i="3">ÊäΩÂ•ñÊù°‰ª∂Ôºö <br> ËøûÁª≠Á≠æÂà∞3Â§©‰∏îÊØèÂ§©ÁôªÂΩï„ÄêÈ°πÁõÆÂ∑•Âú∫APP„ÄëÁÇπËØÑ1‰∏™È°πÁõÆÔºåÂç≥ÂèØËé∑Âæó1Ê¨°ÊäΩÂ•ñÊú∫‰ºöÔºõ<br> ËøûÁª≠Á≠æÂà∞5Â§©‰∏îÊØèÂ§©ÁôªÂΩï„ÄêÈ°πÁõÆÂ∑•Âú∫APP„ÄëÁÇπËØÑ1‰∏™È°πÁõÆÔºåÂèØÈ¢ùÂ§ñÂÜçËé∑Âæó1Ê¨°ÊäΩÂ•ñÊú∫‰ºöÔºõ</li>
      <li data-i="4">ÈôêÊó∂ÊäΩÂ•ñÔºö <br> 12Êúà25Êó•13Ôºö00--14Ôºö00Ôºõ <br> 12Êúà27Êó•13Ôºö00--14Ôºö00Ôºõ</li>
      <li data-i="5">Â•ñÂìÅÁ¶èÂà©Ôºö <br> Â∞èÁ±≥ÊâãÊú∫„ÄÅÊ≤ô‰∏òÂ§ßÂ≠¶‚ÄîÊäïËµÑÂ≠¶Èô¢‰ºöÂëò„ÄÅÈ´òÂÆöÁâàÊó†Á∫øÂÖÖÁîµÂô®„ÄÅÈ°πÁõÆÂ∑•Âú∫ËôöÊãüËµÑ‰∫ß‚ÄîÈíªÁü≥Á≠â</li>
      <li data-i="6">Â•ñÂìÅÂÖëÊç¢Ôºö <br> ËØ∑ËÅîÁ≥ªÊäïËµÑ‰∫∫Â∞èÂä©ÊâãÂæÆ‰ø°Ôºà15911030548ÔºâÂÖëÊç¢Ôºõ <br> Êà™Ê≠¢Êó∂Èó¥‰∏∫12Êúà31Êó•24ÁÇπ</li>
      <li data-i="7">Ê¥ªÂä®ÊúÄÁªàËß£ÈáäÊùÉÔºåÂΩíÊ∏ÖÁßëÈ°πÁõÆÂ∑•Âú∫ÊâÄÊúâÔºõ</li>
    </ol>
    <!-- login-dialog -->
    <pem-dia-log type="modal" ref="loginDialog" :showCloseBtn="false">
      <div class="login-wrapper">
        <div class="login-inner">
          <div class="close-btn" @click="closeLogin"></div>
          <div class="title">‰∫≤ÔºåËØ∑ÂÖàËæìÂÖ•ÊâãÊú∫Âè∑~</div>
          <label class="input-item">
            <div class="input-title">ÊâãÊú∫Âè∑</div>
            <input class="phone-input" v-model="phone" placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑" type="tel" maxlength="11" @input="telInput" >
          </label>
          <div class="input-item">
            <label>
              <div class="input-title">È™åËØÅÁ†Å</div>
              <input class="code-input" v-model="code" placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å" type="tel" maxlength="6" @input="telInput">
            </label>
            <div class="code-btn" :class="{'enabled': codeEnabled}" @click="getCode">{{ codeText }}</div>
          </div>
          <div class="login-btn" :class="{'enabled': submitEnabled }" @click="goLogin">Êèê‰∫§</div>
        </div>
      </div>
    </pem-dia-log>
    <!-- note-dialog -->
    <pem-dia-log type="modal" ref="noteDialog" :showCloseBtn="false">
      <div class="note-wrapper">
        <div class="close-btn" @click="noteDialogHide"></div>
        <h4 class="note-title">Ê∏©È¶®ÊèêÁ§∫</h4>
        <p class="note-text" v-html="noteText"></p>
        <p class="note-btn" @click="noteDialogSure">{{ 'Â•ΩÁöÑ' }}</p>
      </div>
    </pem-dia-log>
    <!-- toast -->
    <pem-toast ref="toastEle" :text="toastText"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import PemDiaLog from 'components/dialog/dialog'
import { MiniSiteApi } from 'api'
import { setTitle, validatePhone } from 'common/js/util'
import storage from 'common/js/storage.js'

export default {
  data () {
    return {
      sdphone: storage.get('sdphone') || '',
      showDraw: false,
      phone: null,
      code: null,
      codeText: 'Ëé∑ÂèñÈ™åËØÅÁ†Å',
      codeEnabled: false,
      submitEnabled: false,
      time: 60,
      timer: null,
      toastText: '',
      noteType: 0,
      noteText: ''
    }
  },
  components: {
    PemScrollView,
    PemToast,
    PemDiaLog
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('Âú£ËØûÂòâÂπ¥Âçé')
      this.wxShare({
        title: 'È°πÁõÆÂ∑•Âú∫„ÄêÊäïËµÑ‰∫∫Âú£ËØûÂòâÂπ¥Âçé„Äëüéâüéâüéâ',
        content: 'Âú£ËØûÂ§ßË∂¥ ÔºåÂ£ïÁ§º‰ªªÊÄßÈ¢ÜÔºÅÔºÅÔºÅ',
        imgUrl: 'http://cdn.pemarket.com.cn/operation/christmas-share-icon.png',
        linkUrl: `${window.location.origin}/christmas-activity`
      })
      if (this.sdphone) {
        if (this.sdphone === '15201177607') {
          this.sdphone = '15810857502'
          storage.set('sdphone', '15810857502')
        }
        this.getButtonStatus()
      }
    },
    drawClick () {
      window.location.href = `${window.location.origin}/christmas-draw`
    },
    signClick () {
      if (this.sdphone) {
        this.gosign()
      } else {
        this.showLogin()
      }
    },
    showLogin () {
      this.$refs.loginDialog.show()
    },
    closeLogin () {
      this.$refs.loginDialog.hide()
    },
    telInput () {
      this.phone = (this.phone || '').trim()
      this.code = (this.code || '').trim()
      this.codeEnabled = !!this.phone
      this.submitEnabled = !!(this.phone && this.code)
    },
    getButtonStatus () {
      if (!this.sdphone) return
      this._getButtonStatus({'mobile': this.sdphone}, (data) => {
        if (!data) return
        if (+data.resultcode === -12) {
          this.showDraw = true
        }
      })
    },
    gosign () {
      if (!this.sdphone) return
      this._gosign({'mobile': this.sdphone}, (data) => {
        this.noteType = +(data && data.resultcode) || 0
        if ((+data.resultcode === -16 || +data.resultcode === -18) && +data.candraw === 1) {
          this.showDraw = true
        }
        this.noteDialogShow()
      })
    },
    getCode () {
      if (!this.codeEnabled) return
      if (!validatePhone(this.phone)) {
        this.showToast('ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊâãÊú∫Âè∑')
        return
      }
      this.codeEnabled = false
      this.codeText = `${this.time}sÂêéÈáçËØï`
      this.timer = setInterval(() => {
        this.codeText = `${this.time}sÂêéÈáçËØï`
        if ((this.time--) <= 0) {
          this.time = 60
          this.codeText = 'Ëé∑ÂèñÈ™åËØÅÁ†Å'
          this.codeEnabled = true
          clearInterval(this.timer)
        }
      }, 1000)
      this._checkMobile(this.phone, (data) => {
        if (data.verifyCode) {
          this.code = data.verifyCode.replace(',repeat', '')
          this.telInput()
        }
        if (+data.resultcode === 1) {
          this.codeEnabled = false
        } else {
          let notemsg = {
            '-11': 'ÊâãÊú∫Âè∑‰∏∫Á©∫',
            '-12': 'ÊâãÊú∫Âè∑ËæìÂÖ•‰∏çÊ≠£Á°Æ',
            '-13': 'Ëé∑ÂèñÈ™åËØÅÁ†ÅÊ¨°Êï∞Â§™Â§ö',
            '-14': 'Ëé∑ÂèñÈ™åËØÅÁ†ÅÈó¥ÈöîÂ§™Áü≠'
          }[data.resultcode] || 'Êú™Áü•ÈîôËØØÔºåËØ∑ËÅîÁ≥ªÁÆ°ÁêÜÂëò'
          this.showToast(notemsg)
          this.codeEnabled = true
        }
      })
    },
    goLogin () {
      if (!this.submitEnabled) {
        return
      }
      let regCode = /^\d{6}$/
      if (!validatePhone(this.phone)) {
        this.showToast('ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊâãÊú∫Âè∑')
        return
      }
      if (!regCode.test(this.code)) {
        this.showToast('ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÈ™åËØÅÁ†Å')
        return
      }
      let options = {
        mobile: this.phone,
        code: this.code
      }
      this._login_lx(options, (data) => {
        if (+data.resultcode === 1) {
          this.sdphone = this.phone
          storage.set('sdphone', this.phone)
          this.closeLogin()
        } else {
          let notemsg = {
            '-11': 'ÊâãÊú∫Âè∑‰∏∫Á©∫',
            '-12': 'È™åËØÅÁ†Å‰∏∫Á©∫',
            '-13': 'È™åËØÅÁ†ÅÂ§±Êïà',
            '-14': 'È™åËØÅÁ†ÅÈîôËØØ'
          }[data.resultcode] || 'Êú™Áü•ÈîôËØØÔºåËØ∑ËÅîÁ≥ªÁÆ°ÁêÜÂëò'
          this.showToast(notemsg)
        }
      })
    },
    showToast (txt) {
      if (!txt) return
      this.toastText = txt
      this.$refs.toastEle.show()
    },
    hideToast () {
      this.$refs.toastEle.hide()
    },
    noteDialogShow () {
      this.noteText = {
        '0': 'Á≠æÂà∞Â§±Ë¥•,ËØ∑ÈáçËØï!',
        '-11': '‰∫≤Ôºå‰∏çÂú®Ê¥ªÂä®ÊúüÈó¥Âì¶~',
        '-12': '‰∫≤ÔºåÊú¨Ê¨°Ê¥ªÂä®ÊÇ®ÊöÇ‰∏çÁ¨¶ÂêàÂèÇ‰∏éÊù°‰ª∂ÔºåÊ¨¢ËøéÊÇ®ÂèÇÂä†‰∏ãÊ¨°Ê¥ªÂä®~',
        '-13': 'ÊÅ≠ÂñúÊÇ®Á≠æÂà∞ÊàêÂäüÔºåÊÇ®‰ªäÂ§©ËøòÈúÄÁÇπËØÑ1‰∏™È°πÁõÆÂìü~',
        '-14': 'ÊÅ≠ÂñúÔºåÁ≠æÂà∞ÊàêÂäüÔºåËØ∑ÊòéÂ§©ÁªßÁª≠ÂìüÔºåÂùöÊåÅÂ∞±ÊòØËÉúÂà©~',
        '-15': '‰∫≤ÔºåÊÇ®‰ªäÂ§©Â∑≤ÁªèÁ≠æÂà∞‰∫ÜÂñΩ~',
        '-16': 'ÊÅ≠ÂñúÔºÅÊÇ®Â∑≤Ëé∑ÂæóÊäΩÂ•ñËµÑÊ†ºÔºåÊäΩÂ•ñÊó∂Èó¥Ôºö<br>12Êúà25Êó•‰∏≠Âçà13Ôºö00--14Ôºö00ÊúüÈó¥<br>ËØ∑ÂáÜÊó∂ËøáÊù•ÊäΩÂìü~',
        '-17': '‰∫≤ÔºåÊÅ≠ÂñúÁ≠æÂà∞ÊàêÂäüÔºÅÊÇ®Â∑≤ÈîôËøá‰ªäÂ§©ÁöÑÊäΩÂ•ñÔºå‰∏çËøáÂÜçÂùöÊåÅÁ≠æÂà∞2Â§©Ôºå12Êúà27Êó•13:00--14:00ËøòÊúâ1Ê¨°ÊäΩÂ•ñÊú∫‰ºöÔºåËØ∑ÂáÜÊó∂ËøáÊù•Âìü~',
        '-18': 'ÊÅ≠ÂñúÔºÅÊÇ®Â∑≤Ëé∑ÂæóÊäΩÂ•ñËµÑÊ†ºÔºåÊäΩÂ•ñÊó∂Èó¥Ôºö<br>12Êúà27Êó•‰∏≠Âçà13Ôºö00--14Ôºö00ÊúüÈó¥<br>ËØ∑ÂáÜÊó∂ËøáÊù•ÊäΩÂ•ñÂìü~',
        '-19': '‰∫≤ÔºåÊÅ≠ÂñúÁ≠æÂà∞ÊàêÂäüÔºÅÊÇ®Â∑≤ÈîôËøá‰ªäÂ§©ÁöÑÊäΩÂ•ñÔºåÊ¨¢ËøéÊÇ®ÁªßÁª≠ÂèÇÂä†‰∏ãÊ¨°Ê¥ªÂä®~'
      }[this.noteType]
      if (!this.noteText) return
      this.$refs.noteDialog.show()
    },
    noteDialogHide () {
      this.$refs.noteDialog.hide()
    },
    noteDialogSure () {
      if (this.showDraw) {
        window.location.href = `${window.location.origin}/christmas-draw`
      } else {
        this.noteDialogHide()
      }
    },
    _getButtonStatus (options, callback) {
      MiniSiteApi.getButtonStatus(options).then(data => {
        callback(data)
      })
    },
    _gosign (options, callback) {
      MiniSiteApi.gosign(options).then(data => {
        callback(data)
      })
    },
    _checkMobile (mobile, callback) {
      MiniSiteApi.checkMobel(mobile).then(data => {
        callback(data)
      })
    },
    _login_lx (options, callback) {
      MiniSiteApi.login_lx(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  @keyframes btnAnimate
    0%
      -webkit-transform:scale(1);
      transform:scale(1)
    to
      -webkit-transform:scale(1.1);
      transform:scale(1.1)
  ::-webkit-input-placeholder
    color: #A7A7A7;
  ::-moz-placeholder
    color: #A7A7A7;
  ::-webkit-input-placeholder
    color: #A7A7A7;
  ::-ms-input-placeholder
    color: #A7A7A7;
  .christmas-activity
    position: relative
    font-size:0;
    padding: 10.46rem 0 0.80rem
    bg-image('./images/bt')
    background-size: 100%;
    background-repeat: no-repeat
    background-color: #9B0507;
    max-width: 750px;
    margin: 0 auto;
    .sign-btn, .draw-btn
      position: absolute
      top: 8.86rem
      left: 2.06rem
      width: 3.40rem
      height: 1.28rem
      background-size: 100% 100%
      background-repeat: no-repeat
      -webkit-animation: btnAnimate .4s infinite alternate forwards;
      animation: btnAnimate .4s infinite alternate forwards;
    .sign-btn
      bg-image('./images/btn-sign')
    .draw-btn
      bg-image('./images/btn-draw')
    .title
      padding-bottom: 0.24rem
      text-align: center
      span
        position: relative
        display: inline-block
        font-family: PingFangSC-Medium;
        font-size: 0.32rem
        color: #FFFFFF;
        line-height: 0.44rem 
        padding: 0 0.28rem
        &::before, &::after
          content: ''
          position: absolute
          top: 50%
          margin-top: -0.04rem
          background: #FFFFFF;
          width: 0.08rem
          height: 0.08rem
          border-radius: 100%
        &::before
          left: 0
        &::after
          right: 0
    .rules
      font-family: PingFangSC-Regular;
      font-size: 0.24rem;
      color: #FFFFFF;
      line-height: 0.38rem
      li
        position: relative
        padding: 0 0.34rem 0.20rem 0.74rem
        &.no-order
          &::before
           display: none
        &::before
          content: "" attr(data-i) "„ÄÅ"
          position: absolute
          top: 0
          left: 0.34rem
    .login-wrapper
      .login-inner
        width: 295px
        height: 215px
        background: #ffffff
        border-radius: 8px
        .close-btn
          position: absolute
          top: 0
          right: 0
          width: 42px
          height: 42px
          bg-image('./images/close')
          background-size: 12px 12px
          background-repeat: no-repeat
          background-position: center
        .title
          font-family: PingFangSC-Regular;
          font-size: 15px;
          color: #202020;
          text-align: center
          padding: 32px 0 25px
        .input-item
          position: relative
          display: block
          min-height: 26px 
          padding-bottom: 20px
          .input-title
            position absolute
            left: 24px
            top: 0
            font-size: 13px;
            color: #333333;
            line-height: 26px
            padding-right: 8px
            bg-image('./images/input-require')
            background-size: 7px 9px
            background-repeat: no-repeat
            background-position: right center
          input
            box-sizing: border-box
            position absolute
            left: 80px
            background: #F5F5F5;
            border-radius: 2px;
            width: 191px;
            height: 26px;
            line-height: 26px;
            font-size: 11px;
            padding: 5px 8px
            &.code-input
              width: 115px
          .code-btn
            position absolute
            right: 24px
            top: 0
            width: 70px
            height: 25px
            line-height: 25px
            text-align: center
            border: 0.5px solid #A7A7A7;
            border-radius: 2px;
            color: #A7A7A7
            font-size: 12px
            &.enabled
              color: #FFC131
              border-color: #FFC131
        .login-btn
          position: absolute
          bottom : 0
          left: 0
          width: 100%
          height: 45px
          line-height: 45px
          border-top: 1px solid #DBDBDB
          font-family: PingFangSC-Regular;
          font-size: 15px;
          color: #A7A7A7;
          letter-spacing: -0.05px;
          text-align: center
          &.enabled
            color: #FFD100;
    .note-wrapper
      box-sizing: border-box
      width: 295px
      background-color: #ffffff
      padding: 32px 35px 46px
      text-align: center
      border-radius: 8px;
      .note-title
        font-size: 15px
        font-weight: bold
        color: #202020;
      .note-text
        font-size: 13px
        color: #333333
        line-height: 20px 
        padding: 25px 0
      .note-btn
        position: absolute
        left: 0
        bottom: 0
        width: 100%
        height: 45px
        line-height: 45px
        font-size: 15px;
        color: #FFC131;
        border-top: 0.5px solid #DBDBDB;
      .close-btn
        position: absolute
        top: 0
        right: 0
        width: 42px
        height: 42px
        bg-image('./images/close')
        background-size: 12px 12px
        background-repeat: no-repeat
        background-position: center
</style>



